<template>
<!-- 我的收藏漫画内容部分 -->
    <div>
        <h1 v-show="$store.state.books.isnull_hint">暂无收藏</h1>
    <div class="content">
        <div class="collect"    v-for="(item,index) in $store.state.books.my_collect" :key="'collect'+index">
            <div class="pic" @click="$store.commit('books/details',{item})">
            <img v-bind:src="item.src" alt="">
            </div>
            <div class="word" @click="$store.commit('books/details',{item})">
            <strong>{{item.name}}</strong>
            <small>{{item.num}}</small>
            <small>{{item.sort}}</small>
            <small>{{item.details}}</small>
        </div>
        <van-checkbox :value="item.isselect" @click="$store.commit('books/select',{index})"></van-checkbox>
        </div>
         
    </div>
    </div>
  
</template>
<script>
export default {
     data(){
        return{
            checked:false,
        }
    },
    watch:{
      "$store.state.books.isdetails":function(newvalue){
            if(newvalue==true){
                this.$router.push({path:"/details"});
            }
        }
    },
    beforeDestroy(){
       this.$store.commit('books/jump_details');
    },
}
</script>
<style lang="scss" scoped>
        @mixin collect{
            background-color:white;
            margin-bottom:0.25rem;
            padding:0.25rem;
            display:flex;
            justify-content: center;
            position: relative;
            .pic{
            img{
                display:block;
                width:3.5rem;
                height:4.675rem;
                margin-right:0.5rem;
            }
            }
            .word{
                overflow:hidden;
                width:70%;
            strong{
                display:block;
                color:#535252;
                font-size:0.8rem;
                padding-top:0.25rem;
                padding-right:2rem;
                line-height: 150%;

            }
            small{
                display:block;
                font-size:0.6rem;
                color:#969696;
                padding-top: 1px;
                white-space:nowrap;
                overflow:hidden;
                text-overflow:ellipsis;
                line-height: 1rem;
            }
            }
        }
    h1{
        color:#ccc;
        text-align: center;
    }
    .content{
        padding:1rem 0.25rem 0.25rem 0.25rem;
        .collect{
            
            @include collect;
        }
       
    }
</style>