<template>
<!-- 我的收藏尾部 -->
    <div>
        <van-submit-bar button-text="删除"  @submit="onClickButton">
            <van-checkbox v-model="checked" @click="all_select">全选</van-checkbox>
        </van-submit-bar>
    </div>
</template>
<script>
export default {
    data(){
        return{
            checked:false,
        }
    },
    methods:{
        all_select(){
            if(this.checked==false){
                this.checked=true;
            }else{
                this.checked=false;
            }
            this.$store.commit('books/isall',this.checked);
        },
        onClickButton(){
            this.$store.commit('books/del');
        }
    }
    
}
</script>
<style lang="scss" scoped>
    .van-checkbox{
        margin:0 40% 0 0;
    }
    
</style>